.mobileBackground {

    width: 100%;
    height: 100vh;
    background-color: #fbf0d1;
    padding: 20px 5px 5px;
    overflow-y: auto;
    overflow-x: hidden;

}

.allPage {
    width: 100%;
    height: 100%;
    padding: 5px;
    overflow-y: auto;
    overflow-x: hidden;
}

.roomList {
    width: 100%;
    height: 60%;
    overflow-y: scroll;
    overflow-x: hidden;
    display: flex;
    flex-wrap: wrap;

    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.3);

    gap: 10px;
    align-items: center;
    justify-content: center;

    padding-top: 15px;
    top: 0;
    left: 0;
}

.roomList::-webkit-scrollbar {
    width: 3px;
}

.signInBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%);
}

.hintText {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: fit-content;
    background-color: var(--bright_Y);
    text-align: center;
    z-index: 50;
    font-size: 18px;
    font-weight: 600;
    font-family: "Play", sans-serif;
    color: #01000E
}

.drawingBox {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.room_card {
    border-radius: 15px;

    border: none;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    min-width: 200px;
    height: 50px;
    font-size: 20px;
    font-weight: 600;
    display: inline-flex;
    justify-content: start;
    padding: 6px 12px;
    align-items: center;
    gap: 25px;

    &.yl {
        background: var(--bright_Y);
        color: #000000
    }

    &.org {
        background: var(--light_Or);
        color: #000000
    }

    &.gr {
        background: var(--light_Gr);
        color: #fff
    }
    &:hover{
        opacity: .8;
    }

    &.noHover:hover{
        opacity: 1 !important;
    }
}