.loading_Background {
    position: absolute;
    top: 5vh;
    left: 0;
    z-index: 1500;
    width: 100%;
    height: 90vh;
    // background-color: #fbf0d1;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dotGroup {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 60px;
    width: 300px;
    height: 60px;
    position: relative;
    z-index: 1;

}

.circle {
    width: 30px;
    height: 30px;
    position: absolute;
    border-radius: 50%;
    background-color: var(--light_Or);
    left: 15%;
    transform-origin: 50%;
    animation: circle7124 .5s alternate infinite ease;

    &.white{
        background-color: var(--white);

    }

}

@keyframes circle7124 {
    0% {
        top: 60px;
        height: 5px;
        border-radius: 50px 50px 25px 25px;
        transform: scaleX(1.7);
    }

    40% {
        height: 20px;
        border-radius: 50%;
        transform: scaleX(1);
    }

    100% {
        top: 0%;
    }
}

.circle:nth-child(2) {
    left: 45%;
    animation-delay: .2s;
}

.circle:nth-child(3) {
    left: 75%;
    animation-delay: .3s;
}

.shadow {
    width: 30px;
    height: 4px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.9);
    position: absolute;
    top: 62px;
    transform-origin: 50%;
    z-index: -1;
    left: 15%;
    filter: blur(1px);
    animation: shadow046 .5s alternate infinite ease;
}

@keyframes shadow046 {
    0% {
        transform: scaleX(1.5);
    }

    40% {
        transform: scaleX(1);
        opacity: .7;
    }

    100% {
        transform: scaleX(.2);
        opacity: .4;
    }
}

.shadow:nth-child(4) {
    left: 45%;
    animation-delay: .2s
}

.shadow:nth-child(5) {
    left: auto;
    right: 15%;
    animation-delay: .3s;
}